$main-color: #1eacc7;
@mixin btn_hover {
    &:hover {
        background-image: none;
        background-color: $main-color; } }

* {
    font-family: Roboto, sans-serif; }
header {
    position: relative; }
nav {
    position: fixed;
    z-index: 4;
    width: 100%;
    height: 50px;
    background-color: rgba(33, 33, 33, 1); }
.menu {
    display: flex;
    justify-content: space-between;
    padding: 13px 70px;
    margin: 0;
    list-style-type: none;
    z-index: 1;
    .menu_link {
        color: #FFFFFF;
        font-size: 15px;
        font-weight: 100; }
    .menu_item {
        border-right: 2px dotted black;
        padding-right: 28px; } }
.subheader {
    position: absolute;
    width: 100%;
    height: 84px;
    margin-top: 50px;
    z-index: 3;
    background-color: rgba(0, 0, 0, 0.6);
    .subheader_logo {
        display: block;
        max-width: 100%;
        margin-top: 16px; }
    .subheader_oficial {
        opacity: 0.5;
        font-size: 13px;
        font-weight: 100;
        text-align: left;
        color: #FFFFFF;
        margin-top: 6px; }
    .subheader_call {
        display: block;
        color: #FFFFFF;
        font-size: 12px;
        font-weight: 100;
        margin-top: 19px;
        text-align: right; }
    .subheader_phone {
        display: block;
        margin-top: 8px;
        color: #FFFFFF;
        font-size: 22px;
        font-weight: 100;
        text-transform: uppercase;
        text-align: right;
        line-height: 12px; }
    .subheader_btn {
        display: block;
        margin-top: 19px;
        width: 172px;
        height: 46px;
        padding: 15px;
        box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
        border: none;
        border-radius: 4px;
        background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
        color: #FFFFFF;
        font-size: 14px;
        font-weight: 400;
        text-transform: uppercase;
        text-align: center;
        @include btn_hover; } }
.promo {
