        &_subtitle {
            font-size: 24px;
            font-weight: 400;
            text-transform: uppercase;
            text-align: center;
            color: #FFFFFF; }
        &_plus {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            bottom: 35px;
            left: 50%;
            transform: translateX(-50%);
